import 'core-js/fn/object/assign';
import React from 'react';
import ReactDOM from 'react-dom';
import {HashRouter, Route, Link, Switch} from 'react-router-dom';
import SS from 'parsec-ss';
import Config from 'config';

import App from './components/Main';

//()
const handleAuth = function (nextState, replace) {
  debugger;
  if (SS.get(Config.USER_TOKEN) == null) {
    //
    replace({ pathname: 'login' });
  }
}


const rootRoute = {
  childRoutes: [
    { path: 'login', getComponent(nextState, cb) { require.ensure([], (require) => { cb(null, require('./components/LoginComponent').default) }); } },
    {
      path: '/',
      breadcrumbName: '',
      onEnter: handleAuth,
      getComponent(nextState, cb) {
        require.ensure([], (require) => {
          cb(null, require('./components/Main').default)
        });
      },
      indexRoute: {
        breadcrumbName:'',
        getComponent(nextState, cb) {
          require.ensure([], (require) => {
            cb(null, require('./components/IndexComponent').default)
          }); //dashboard
        }
      },
      childRoutes: [
        { path: '/profile', breadcrumbName:'', getComponent(nextState, cb) { require.ensure([], (require) => { cb(null, require('./components/LoginComponent').default) }); } }
      ]
    }
  ]
}

// Render the main component into the dom
ReactDOM.render(<HashRouter>
  <App>
    <Route exact path="/" component={require('./components/IndexComponent').default} />
    <Route path="/login" component={require('./components/LoginComponent').default} />
  </App>
</HashRouter>, document.getElementById('app'));



